.pdf-viewer[data-v-f5b516f4] {
	position: relative;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	background: var(--ereader-surface);
}
.pdf-viewer__toolbar[data-v-f5b516f4] {
	flex-shrink: 0;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	gap: 0.5rem 1rem;
	padding: 0.5rem 0.75rem;
	background: var(--ereader-action-bar);
	color: var(--ereader-primary-text);
	border-bottom: 1px solid var(--ereader-border);
}
.pdf-viewer__toolbar-group[data-v-f5b516f4] {
	display: flex;
	align-items: center;
	gap: 0.25rem;
}
.pdf-viewer__toolbar-group--page[data-v-f5b516f4] {
	margin-left: auto;
	flex-shrink: 0;
}
.pdf-viewer__btn[data-v-f5b516f4] {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	min-width: 36px;
	height: 36px;
	padding: 0 0.5rem;
	border: none;
	border-radius: var(--ereader-radius-sm);
	background: transparent;
	color: inherit;
	cursor: pointer;
	transition: background 0.15s;
}
.pdf-viewer__btn[data-v-f5b516f4]:hover:not(:disabled) {
	background: rgba(255, 255, 255, 0.2);
}
.pdf-viewer__btn[data-v-f5b516f4]:disabled {
	opacity: 0.5;
	cursor: not-allowed;
}
.pdf-viewer__btn--text[data-v-f5b516f4] {
	font-size: 0.875rem;
}
.pdf-viewer__toolbar-group--pages[data-v-f5b516f4] {
	display: flex;
	align-items: center;
	gap: 0.25rem;
}
.pdf-viewer__btn--pages[data-v-f5b516f4] {
	min-width: 32px;
	font-size: 0.875rem;
	font-weight: 500;
}
.pdf-viewer__btn--pages-active[data-v-f5b516f4] {
	background: rgba(255, 255, 255, 0.3);
}
.pdf-viewer__icon[data-v-f5b516f4] {
	width: 24px;
	height: 24px;
}
.pdf-viewer__scale[data-v-f5b516f4] {
	min-width: 3.5em;
	font-size: 0.875rem;
	text-align: center;
}
.pdf-viewer__label[data-v-f5b516f4] {
	font-size: 0.875rem;
	white-space: nowrap;
	color: var(--ereader-primary-text);
}
.pdf-viewer__pages-input[data-v-f5b516f4] {
	width: 2.5rem;
	padding: 0.25rem 0.35rem;
	font-size: 0.875rem;
	text-align: center;
	border: 1px solid var(--ereader-border);
	border-radius: var(--ereader-radius-sm);
	background: var(--ereader-surface);
	color: var(--ereader-primary-text);
}
.pdf-viewer__pages-input[data-v-f5b516f4]:focus {
	outline: none;
	border-color: var(--ereader-primary, #1976d2);
}
.pdf-viewer__page-indicator[data-v-f5b516f4] {
	font-size: 0.875rem;
	white-space: nowrap;
}
.pdf-viewer__scroll[data-v-f5b516f4] {
	flex: 1;
	min-height: 0;
	overflow-x: auto;
	overflow-y: auto;
	scroll-behavior: smooth;
	scroll-snap-type: x mandatory;
}
.pdf-viewer__container[data-v-f5b516f4] {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: flex-start;
	justify-content: flex-start;
	padding: 1rem;
	gap: 1rem;
	min-height: min-content;
}
.pdf-viewer__page-wrap[data-v-f5b516f4] {
	position: relative;
	flex: 0 0 auto;
	scroll-snap-align: center;
	scroll-snap-stop: always;
}
.pdf-viewer__text-layer[data-v-f5b516f4] {
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
	line-height: 1;
	user-select: text;
	pointer-events: auto;
}
.pdf-viewer__text-layer span[data-v-f5b516f4] {
	color: transparent;
	position: absolute;
	white-space: pre;
	transform-origin: 0 0;
	pointer-events: auto;
}
.pdf-viewer__page[data-v-f5b516f4] {
	display: block;
	max-width: 100%;
	height: auto;
	box-shadow: var(--ereader-card-shadow);
}
.pdf-viewer__loading[data-v-f5b516f4],
.pdf-viewer__error[data-v-f5b516f4] {
	position: absolute;
	inset: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 2rem;
	text-align: center;
	color: var(--ereader-text-secondary);
	background: var(--ereader-surface);
}
.pdf-viewer__error[data-v-f5b516f4] {
	color: var(--ereader-error);
}
